import React, {useEffect, useState} from "react";

import LinkContainer from "../../components/Shared/LinkContainer";
import VideoOverlay from "../../components/Shared/VideoOverlay";
import StyledLink from "../../components/Shared/StyledLink";
import Container from "../../components/Shared/Container";
import LinkBox from "../../components/Shared/LinkBox";
import Video from "../../components/Shared/Video";

import routes from '../routes';

import { useHistory, useLocation } from "react-router";


const MovieWithRedirectOnFinishView = () => {
	const [movieCompleted, setMovieCompleted] = useState(false);
	const [pushLink, setPushLink] = useState(null);

	const history = useHistory();
	const location = useLocation();

	const thisRouteObject = routes.find(route => route.path === location.pathname);

	useEffect(() => {
		if (movieCompleted && pushLink) {
			history.push(pushLink);
		}
	}, [movieCompleted])

	return (
		<Container>
			<Video
				autoPlay={'autoplay'}
				onEnded={() => {
					setMovieCompleted(true);
				}}
			>
				<source src={thisRouteObject.video} type="video/mp4"/>
			</Video>
			<VideoOverlay>
				<LinkContainer height={'20%'}>
					<LinkBox>
						<StyledLink
							to={movieCompleted ? thisRouteObject.leftButton.linkTo : location.pathname}
							size={'2rem'}
							onClick={() => setPushLink(thisRouteObject.leftButton.linkTo)}
						>
							{thisRouteObject.leftButton.label}
						</StyledLink>
					</LinkBox>
					<LinkBox>
						<StyledLink
							to={movieCompleted ? thisRouteObject.rightButton.linkTo : location.pathname}
							size={'2rem'}
							onClick={() => setPushLink(thisRouteObject.rightButton.linkTo)}
						>
							{thisRouteObject.rightButton.label}
						</StyledLink>
					</LinkBox>
				</LinkContainer>
			</VideoOverlay>
		</Container>
	)
};

export default MovieWithRedirectOnFinishView;